import { Link } from 'react-router-dom'

import { Box, Card, CardActionArea, CardMedia, Chip, Stack, Typography, useTheme } from '@mui/material'

import getDateOrNull from 'src/utils/getDateOrNull'
import getImgFromHtml from 'src/utils/getImgFromHtml'
import getRecruitDDayLabel from 'src/utils/getRecruitDDayLabel'
import { htmlToPlainTextWithDOM } from 'src/utils/htmlToPlainText'

import { type ClubId } from 'src/models/club'

import { useGetRecruitInformation } from 'src/hooks/api/club'

const today = new Date()

const RecruitPreviewCard: React.FC<{
  clubId: ClubId
  isDesktopOrTablet: boolean
}> = ({ clubId, isDesktopOrTablet }) => {
  const { data: recruitInfo } = useGetRecruitInformation(clubId, false, today, 0)
  const theme = useTheme()

  const startAt = recruitInfo ? new Date(recruitInfo.startAt) : today
  const endAt = recruitInfo ? getDateOrNull(recruitInfo.endAt) : today
  const isBeforeDeadline = !!recruitInfo && (!endAt || today <= endAt)
  const isRecruiting = recruitInfo ? startAt <= today && isBeforeDeadline : false
  const dDay =
    isBeforeDeadline && endAt ? Math.ceil((endAt.getTime() - today.getTime()) / (24 * 60 * 60 * 1000)) : undefined

  const recruitImgSrc = recruitInfo ? getImgFromHtml(recruitInfo.contents) : ''

  return (
    <Card>
      {isBeforeDeadline ? (
        <CardActionArea
          component={Link}
          to={`/club/${clubId}/recruit/${recruitInfo.id}`}
          sx={{ display: 'flex', flexDirection: 'row', alignItems: 'stretch', padding: 2 }}
        >
          <Stack direction="column" flex="1" spacing={1}>
            <Typography fontSize={15} fontWeight={'bold'} overflow="hidden" textOverflow="ellipsis">
              {recruitInfo.title}
            </Typography>
            <Box
              overflow="hidden"
              textOverflow="ellipsis"
              sx={{
                display: '-webkit-box',
                WebkitLineClamp: 4,
                WebkitBoxOrient: 'vertical',
                whiteSpace: 'normal'
              }}
            >
              <Typography>{htmlToPlainTextWithDOM(recruitInfo.contents)}</Typography>
            </Box>
            <Stack direction="row" alignItems="center" spacing={1}>
              <Chip
                label={isRecruiting ? '모집중' : '모집예정'}
                size="small"
                variant={isRecruiting ? 'filled' : 'outlined'}
                color="primary"
                sx={{
                  padding: 0.5,
                  fontSize: isDesktopOrTablet ? 13 : 11,
                  paddingTop: 0.7
                }}
              />
              <Typography
                variant="body2"
                fontSize={15}
                color={dDay === 0 ? theme.colors.alpha.black[50] : theme.colors.primary.main}
                fontWeight="bold"
              >
                {getRecruitDDayLabel(dDay)}
              </Typography>
            </Stack>
          </Stack>
          {recruitImgSrc && (
            <CardMedia
              sx={{
                width: 120,
                height: 150,
                objectFit: 'cover',
                objectPosition: 'center',
                minWidth: 0,
                marginLeft: 2,
                borderRadius: 0.5
              }}
              component="img"
              image={recruitImgSrc}
            ></CardMedia>
          )}
        </CardActionArea>
      ) : (
        <Typography color="text.secondary" p={2}>
          지금은 모집 기간이 아니에요.
        </Typography>
      )}
    </Card>
  )
}

export default RecruitPreviewCard
