import Masonry from 'react-masonry-css'

import { Box, Card, Stack, Typography } from '@mui/material'

import { type ClubId } from 'src/models/club'

import SemesterChip from 'src/components/SemesterChip'

import { useGetClubReviews } from 'src/hooks/api/review'
import { type ClubReview } from 'src/responses/review'

const breakpointColumns = {
  default: 3,
  960: 2,
  600: 1
}

const ReviewGrid: React.FC<{ clubId: ClubId }> = ({ clubId }) => {
  const { data: reviews } = useGetClubReviews(clubId)

  return reviews && reviews.length > 0 ? (
    <Box
      component={Masonry}
      breakpointCols={breakpointColumns}
      sx={{
        display: 'flex',
        marginLeft: '-16px',
        width: 'auto',
        '& > div': {
          paddingLeft: '16px',
          backgroundClip: 'padding-box'
        }
      }}
    >
      {reviews.map(review => (
        <Box key={review.id} mb={2}>
          <ReviewCard {...review} />
        </Box>
      ))}
    </Box>
  ) : (
    <Card sx={{ p: 2 }}>
      <Typography color="text.secondary">아직 작성된 후기가 없어요.</Typography>
    </Card>
  )
}

export default ReviewGrid

const ReviewCard: React.FC<ClubReview> = review => {
  return (
    <Card sx={{ p: 2 }}>
      <Stack spacing={1}>
        <Stack direction="row" justifyContent="space-between" alignItems="center" mb={0.6}>
          <Typography variant="body2" color="text.secondary">
            from. {review.userNickname}
          </Typography>
          <SemesterChip year={review.year} semester={review.semester} />
        </Stack>
        <Typography variant="body1" sx={{ flexGrow: 1 }}>
          {review.content}
        </Typography>
        {review.recommendedPerson && (
          <Typography variant="body2" color="#FF9393">
            # {review.recommendedPerson}에게 추천!
          </Typography>
        )}
      </Stack>
    </Card>
  )
}
